import { SET_FILTER, Filters } from './../actions/actions';

const { SHOW_ALL } = Filters

const filter = (state = SHOW_ALL, action) => {
    switch (action.type) {
      case SET_FILTER:
        return action.filter
      default:
        return state
    }
  }
  
  export default filter


